          text: '講美村舊名「港尾」，因地理位置位處於鎮海港的尾端，早期村民逐寮而居，久成了聚落而命名為港尾，在1955年才正式更名為「講美」。<br><br>講美鎮海社區海堤公園是連接講美社區及鎮海社區的海堤公園，在民國88年5月白沙鄉講美與鎮海社區總體營造推動下，設計了這座外觀造形是OK字樣的社區標誌，為此座海堤公園增添了趣味、讓人印象深刻的地標。台灣知名的樂團五月天也曾在此錄製過節目，唱過一首《OK啦》，相當符合此地此景。<br><br>講美OK標誌以水泥建材為基底，點綴澎湖在地特色咾咕石，途經203縣道過了永安橋便可注意到，這座藏身在矮牆與植栽後顯目的OK標誌；一旁還有木造的階梯與涼亭觀景台，供遊客在此稍作歇息，欣賞眼前的海天景色，享受片刻的寧靜與悠閒。<br><br>越過海堤，印入眼簾的是一望無際、廣闊的貝殼沙灘海景，每日潮汐的變化，讓這座海堤公園的景色也隨著潮汐而展現不同的風貌。漲潮時，細緻平整的貝殼沙灘與碧藍、清澈透明的海水就靜靜地躺在此地，隨風隨潮汐起伏。眺望遠方海面，依稀還能看見坪之嶼和秘境之一的講美海中崗哨，各佔據海面一角，孤獨而美麗的存在著。',
