          title: '後寮威靈殿、魑魅魍魎石敢當',
          text: '後寮威靈宮，主祀保生大帝，又稱大道公、吳真君，字華基，號雲東，祖居福建泉州府同安縣明盛鄉基善里白礁村，今屬漳州市龍海縣角美鎮。幼年時聰穎絕倫，夙具道性，稍長博通天文地理、禮樂諸書，尤其擅長醫術，醫名逐漸傳播民間。<br><br>白沙鄉後寮威靈宮建廟歷史迄今400多年，為全澎湖供奉保生大帝之濫觴。保生大帝威靈顯赫、神威浩瀚，香火先後傳遞至湖西鄉南北寮兩村，至今信仰依然鼎盛，香火裊裊，是後寮全境的信仰與守護神。<br><br>肇建於明朝萬曆30（西元1602）年的後寮威靈宮，經歷年歲月更迭、改朝換代、廟貌修繕多次，民國以後經地方紳、鄉老委員提議重建，第二次在民國21年重建，歷經63年歲月，再於民國84年出火後，民國85年展開重建工程，民國87年完工；廟貌巍峨、富麗莊嚴，在廟埕一隅還有設置歷次整建碑。<br><br>在後寮威靈宮旁，還有一全台最大碑形石敢當，高近200公分，於清道光22（西元1842）年興建；緣由來自於在清道光21（西元1841）年秋冬，傳後寮威靈宮前，約後寮與通樑之間稱「風坑口」處，山精鬼魅四處作祟，村民不堪其擾，而奉香祈求保生大帝指點，依保生大帝指示降乩示設「魑魅魍魎」（四字上加雨字）碑以鎮邪祟、保境安民，果真銷聲匿跡至今不侵。<br><br>碑初時於鎮煞處，後移至威靈宮左側，並隨廟貌修繕多次，於97年至98年時先後行三層基做修繕，鋪石板並刻鹿鶴等吉祥圖，另置麒麟一對及護欄呈現貌。<br><br>「魑魅魍魎」（四字上加雨字）碑為百年厭勝物，體積也是全台符籙式類石敢當之最，而「石敢當」是屬於海島地區居民獨特發展出來的信仰象徵。',
