        {
          img: './images/後寮三八垵魚灶.jpg',
          html: '三八垵魚灶',
          title: '後寮<br>三八垵魚灶',
          text: '澎湖四面環海擁有豐富的自然資源，日治時期日本人引進了魚獲加工技術，將原先低經濟價值的魚獲，經過加工技術處理，配銷至台灣本島、國外，讓澎湖魚獲產業蓬勃發展。而「魚灶」便是在此經濟發展中占有重要地位的建築型態。<br><br>民國50年是澎湖傳統漁業最發達的時期，資源環境的變化、技術的革新、市場需求量，讓「魚灶」建築也隨漁業的發達而林立，各漁港沿岸或村內都設有「魚灶」。季節所產的臭肉魚和丁香魚是主要的漁獲來源，村里的婦人將捕獲的漁產在魚灶進行加工，先用鹽水浸泡洗淨後，放在圓形的竹篩加水蒸煮後再曝曬風乾，外銷台灣本島或是日本。<br><br>70年代以後，由於漁獲資源的減少、市場需求的變化、人口外流等因素，傳統魚灶產業逐漸沒落，連帶影響魚灶建築也隨之走下坡而荒廢，在澎湖老一輩記憶中，老老少少環繞著魚灶生活的熱鬧情景，也隨著魚灶的殘破而褪色、遺留在記憶的軌跡。<br><br>2017年7月在白沙鄉公所與社區發展協會共同努力下，「後寮三八垵魚灶」重新整修啟用，為使百年的魚灶歷史能夠繼續保存傳遞，魚灶的重現不僅讓老一輩的村民回味往日時光，更是讓後代子孫了解早期漁業社會居民的生活方式，富有教育意義外，更期望能帶動、串連周遭觀光景點，帶來更多遊客促進白沙鄉地區的觀光產業發展。<br><br>位於白沙後寮舊碼頭的三八垵魚灶，就在秘境「天堂路」旁，民眾前往天堂路時別忘了可順道來欣賞傳統的魚灶建築。後寮的三八垵魚灶不僅有魚灶配備，還巧妙的利用澎湖在地浮球及其他回收品創造了四尊人偶，人偶們姿態各異、各司其職，生動靈現地重現了傳統魚灶工作情形。<br><br>魚灶旁的珊瑚沙灘還有停泊一艘彩繪漁船，漁船不僅串聯著魚灶的歷史，色彩飽滿鮮豔的漁船在澎湖閃耀的陽光照射下，襯著藍天、白雲、沙灘，是澎湖最美的景色之一。',
