          text: '目斗嶼與南側的吉貝嶼相隔7公里。全島周長843公尺，面積僅0.0244平方公里，是一個迷你小島，該島以目斗嶼燈塔聞名，島上除燈塔工作人員外，無其他住戶。<br><br>目斗嶼位於吉貝嶼北方，為澎湖群島最北的島嶼，因形狀好似人的眼睛而得名。另有一說，由於該島是玄武岩方山台地構成，形狀亦如木匠使用的墨斗，所以早期稱為「墨斗嶼」，後因讀音差池而變成「目斗嶼」，再加上位處澎湖群島的最北端，因此又稱為「北島」。<br><br>附近有許多暗礁，以往常發生船難，所以在清代光緒年間設立目斗嶼燈塔，為船隻指引方向，至今已超過百年。目斗嶼燈塔高40公尺，黑白相間的塔身矗立在黑褐色的岩礁上，自然散發一股巍峨的氣勢。<br><br>目斗嶼燈塔是日本人統治台灣後，在澎湖群島興建的第一座燈塔。目斗嶼燈塔的塔高39.9公尺，為台灣地區塔高最高的銑鐵燈塔，也是遠東地區最高的銑鐵鑄造燈塔（居住者為燈塔看守管理員）。<br><br>其取水口設在燈塔內部，將燈塔及宿舍屋頂蓄積的雨水源直接引進塔內，是台灣地區唯一將蓄水池取水口設在塔內的燈塔。目前島上無電，電力全靠三部發電機自行發電，提供燈塔運轉。<br><br>目斗嶼燈塔於民國90年澎湖縣歷史建築票選中被選為澎湖縣歷史建築十景之一！',
          modalimg: './images/目斗嶼.jpg'
        },
        {
          img: './images/城前沙灘.jpg',
          html: '城前沙灘',
          title: '城前沙灘',
          text: '位於北環白沙鄉的城前村，前方就是澎湖的內海海域，這裡有一個寧靜的城前沙灘。<br><br>城前沙灘地勢較低，沙灘外型似月牙狀，且背對東北季風，是在冬季裡澎湖少數不受影響的沙灘，且也鮮少人為影響，擁有自然的海岸線、潮間帶豐富生態，從這裡可以看到馬公市、中屯風車、西嶼、大倉島。<br><br>這裡是貝殼沙砂質，踩起來很舒服，這裡屬緩坡沙灘與潮間帶的地形，在退潮時，會裸露出一大片潮間帶，可以進行潮間帶活動。<br><br>一旁則是城前漁港，規模不大，港邊可見小漁船和載牡蠣的養蚵船筏停靠著，從這裡看海，可以看到一大片的蚵架，在退潮時，還可以看到漁人們身背齊全器具，前往蚵架整理，這一帶的城前、講美牡蠣都很有名也很美味。<br><br>在城前沙灘的不起眼角落，還有著一個成勾狀的沙嘴，被廣大的潮間帶給包圍著，漲退潮差異甚大的城前沙灘美景，獨享海角一隅，踩踏沙灘、探索潮間帶。',
