        {
          img: './images/白沙嶼.jpg',
          html: '白沙嶼',
          title: '白沙嶼',
          text: '地質景觀多變，南方有美麗的白色沙灘，是典型的沙嘴地形，還有因受到海水侵蝕而形成的豆腐狀的灘岩及壺穴；東岸的塔狀及彎曲狀的柱狀玄武岩，造型均十分特殊。<br><br>從白沙歧頭港出發，往東北方行駛，約在鳥嶼北方4公里的海域，可以看到擁有一大片沙灘的島嶼，即是「小白沙嶼」。這片沙灘是個典型的沙嘴地形，長度雖然不長，但沙層厚、沙量多，曾是鳥嶼地區建築時的取沙之處，但劃為保留區後，已不准再開採。<br><br>由島的西南海域眺望小白沙嶼，有如一隻伏在海中的貓，鳥嶼人將它稱做「貓嶼」，此貓嶼跟我們一般所稱南海的貓嶼是不一樣的。<br><br>本島南部的熔岩台地高約15～20公尺，由東向南綿延約250公尺，其玄武岩柱狀節理發達，在島嶼東岸海崖為火山頸地形，高約20公尺，氣勢磅礡。東側海蝕平台有一條彎曲的岩脈，附近尚有一低平火山口地形。西邊的海蝕平台上則有一處灘岩，因受海水侵蝕而形成豆腐狀，與散佈其間的壺穴相互輝映，十分有趣。<br><br>由於小白沙嶼是保留區，未經申請不得登島，於是紅燕鷗、白眉燕鷗、鳳頭燕鷗選擇此處棲息。每當春末夏初時，牠們在島上不同的地點繁殖，白眉燕鷗選在岩石區，紅燕鷗、鳳頭燕鷗則選擇草地，各自互不干擾，彼此相安無事的求偶、交配、產卵、育雛、覓食，這種繁殖景象，可說是每年此地的一大盛事，每當船隻航行此處海域，觀賞島嶼與群鷗飛翔的美景，自是人生一大享受。',
          modalimg: './images/白沙嶼.jpg'
