          text: '由大白沙嶼往北大約2公里的海面上，有一座典型的方山地形島嶼，因為島上大部分是大型石板 ，平坦無痕跡，就像是打鐵用的砧板，所以稱他為鐵砧嶼。島的高度大約5公尺，四周都是垂直海崖，島最高除了如砧板狀的玄武岩外，還有許多造形奇妙的蜂窩岩，也因為民國五十幾年曾經是國軍的靶場，因此殘留許多鐵片和岩石的碎片。<br><br>鐵砧嶼位於澎湖本島西北方外海，全島由玄武岩構成，外型與砧板神似，因而得名為鐵砧嶼。由於冬天東北季風的緣故，海浪長期沖刷鐵砧嶼北面海岸，而形成10餘公尺的海蝕洞，是全澎湖最大的海蝕洞，此洞位置特殊，長年無法照射到陽光，因此即使在夏季，洞內溫度也比戶外約低個三至五度，經常吸引到訪的遊客前往避暑休息，使人彷彿置身於冷氣房，因此得名冷氣洞。<br><br>由北面海上看鐵砧嶼，可以清楚的看到島海崖的上部是玄武岩，下面是火山角礫岩 ，由於火山角礫岩岩性較為脆弱，以致於被冬季海浪沖蝕，在北面的海崖下面形成了巨大的海蝕洞，小型遊艇與漁船都可以開入。島上的潮間帶比較淺短，海蝕洞附近幾乎沒有海蝕平台，只有東邊海崖下有一片約5公尺寬的平台，和南邊、西邊小面積的礫石灘，所以較少漁民上島採集螺、貝類，因此每年都匯有千隻以上的白眉燕鷗在島上進行繁殖。每當船隻靠近岸邊，成群的白眉燕鷗會陸續由島上飛出，並且發出如小狗且低沉的警戒聲，最後全部集結在船隻的上空盤旋，形成一種非常奇妙的景觀。',
          modalimg: './images/鐵砧嶼.jpg'
        },
