          text: '大倉嶼位在澎湖的中心點，與馬公市船程約15分鐘，屬二級離島，面積約0.1656平方公里（16.56公頃），可徒步或單車漫遊全島。大倉嶼整體地型北高南低，東北兩側有陡峭海崖玄武岩柱，西南方是平緩的坡面入海，整座島嶼被潮間帶和海蝕平台環繞。在島上四周可以環顧馬公觀音亭、白沙中屯風車、西嶼跨海大橋等景點。<br><br>位於大倉漁港的西邊有著澎湖群島特有的米白色貝殼沙灘，是遊客必訪景點。亦因潮汐潮差大，擁有豐富的潮間帶海洋資源。村內公共設施有衛生室、活動中心、遊客中心，原大倉國小於2005年08月廢校；故島上的教育、生活都與馬公本島密不可分。島上信仰中心為水仙宮。',
